<template>
  <div class="mt-spacing-200 flex w-full flex-1 flex-col overflow-y-auto">
    <div class="w-full flex-1" :class="horizontalPadding">
      <table class="min-w-full flex-1" :class="bgClass">
        <!-- Table Header -->
        <TableHeader
          :headers="headers"
          :sort-key="sortKey"
          :sort-order="sortOrder"
          :bg-class="bgClass"
          @fetch-sorted-data="handleSort"
        />

        <!-- Table Body Slot -->
        <tbody>
          <slot name="table-body"></slot>
        </tbody>
      </table>
      <!-- Infinite Loading Component -->

      <InfiniteLoading
        v-if="infiniteScrollEnabled && currentPage < totalPages"
        @infinite="infiniteHandler"
        class="w-full"
      >
        <template #error="{ trigger }">
          <div
            class="py-spacing-200 text-body/m/relaxed/400 text-content-on-neutral-secondary ipad:text-body/l/relaxed/400"
          >
            An Error occurred. Please
            <a
              href="javascript:;"
              @click="retry(trigger)"
              class="text-body/m/relaxed/500 text-constant-brand-primary hover:text-hover-constant-brand-primary ipad:text-body/l/relaxed/500"
              >click here</a
            >
            to retry.
          </div>
        </template>
        <template #spinner>
          <div class="flex w-full items-center justify-center py-spacing-200">
            <div
              class="h-spacing-400 w-spacing-400 animate-spin text-constant-brand-primary"
            >
              <IconLoader2 />
            </div>
          </div>
        </template>
      </InfiniteLoading>
    </div>
    <!-- Pagination Footer (Visible for pagination mode) -->
    <div
      v-if="!infiniteScrollEnabled"
      class="sticky bottom-spacing-0 left-spacing-0 flex items-center justify-between px-spacing-300 py-spacing-200"
      :class="bgClass"
    >
      <div @click="fetchPage(currentPage - 1)">
        <GeneralButton
          :button-type="'button'"
          :type="'brand'"
          :size="'small'"
          :emphasis="'secondary'"
          :icon-position="'none'"
          :icon="''"
          :image="''"
          :is-disabled="currentPage === 1"
          :is-loading="false"
          :label="'Previous'"
        />
      </div>

      <span class="text-body/l/relaxed/400 text-content-on-neutral-secondary">
        Page {{ currentPage }} of {{ totalPages }}
      </span>

      <div @click="fetchPage(currentPage + 1)">
        <GeneralButton
          :button-type="'button'"
          :type="'brand'"
          :size="'small'"
          :emphasis="'secondary'"
          :icon-position="'none'"
          :icon="''"
          :image="''"
          :is-disabled="currentPage === totalPages"
          :is-loading="false"
          :label="'Next'"
        />
      </div>
    </div>
  </div>
</template>

<script>
import InfiniteLoading from "vue-infinite-loading";
export default {
  components: {
    InfiniteLoading,
    IconLoader2: () => import("@/components/svgIcons/loader-2.vue"),
    TableHeader: () => import("@/components/General/Table/TableHeader.vue"),
    GeneralButton: () => import("@/components/General/CustomButton.vue"),
  },
  props: {
    headers: {
      type: Array,
      required: true,
    },
    currentPage: {
      type: Number,
      required: true,
    },
    totalPages: {
      type: Number,
      required: true,
    },
    totalCount: {
      type: Number,
      required: false,
      default: 0,
    },
    sortKey: {
      type: String,
      required: false,
      default: "",
    },
    sortOrder: {
      type: String,
      required: false,
      default: "",
    },
    bgClass: {
      type: String,
      required: false,
      default: "bg-bg-neutral-default",
    },
    infiniteScrollEnabled: {
      type: Boolean,
      default: true,
      required: false,
    },
    isLoading: {
      type: Boolean,
      default: false,
      required: false,
    },
    error: {
      type: Object,
      default: () => null,
      required: false,
    },
    horizontalPadding: {
      type: String,
      required: false,
      default: "px-spacing-200 ipad:px-spacing-300",
    },
  },
  data() {
    return {
      skipError: false, // Flag to indicate skipping error check
    };
  },
  methods: {
    handleSort(options) {
      this.$emit("fetch-sorted-data", options);
    },
    async infiniteHandler($state) {
      if (!this.skipError && this.error) {
        $state.error();
        return;
      }
      this.skipError = false;
      setTimeout(async () => {
        if (this.currentPage < this.totalPages && !this.isLoading) {
          await this.fetchPage(this.currentPage + 1);
          if (this.error) {
            $state.error();
          } else {
            $state.loaded();
          }
        } else if (this.currentPage >= this.totalPages) {
          $state.complete();
        }
      }, 500);
    },
    async fetchPage(page) {
      if (page > 0 && page <= this.totalPages && !this.isLoading) {
        return new Promise((resolve) => {
          this.$emit("fetch-data", page, resolve);
        });
      }
    },
    retry(trigger) {
      this.skipError = true;
      trigger();
    },
  },
};
</script>
